import { HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { getPortal } from '@iframework/config';
import { RequestJSON, RequestOptions } from './models';

export function getRequest(type: string, userName: string, password: string, token: string, requestDetails: Object): RequestJSON {
	let request: RequestJSON = {
		request: {
			header: {
				type: type,
				username: userName,
				password: password,
				token: token,
				portal: getPortal(),
			},
			requestDetails: requestDetails
		}
	};
	return request;
}

export function mapResponse(response: Observable<any>) {
	return response.map(res => {
		return res.response || res.Response;
	});
}

export function getRequestOptions(): RequestOptions {
	return { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
}